.app {
  	text-align: center;
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.main-content {
	flex: 1;
}

.component-item {
	font-size: 1rem;
	color: #eeeeee;
}

.app-logo {
	animation: App-logo-spin infinite 20s linear;
	height: 40vmin;
}

.app-header, footer {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	height: 2rem;
	color: white;
}

footer {
	justify-content: flex-end;
	flex-direction: row;
	padding: 0 10px 5px 0;
}

footer button {
	outline: none;
	border: 0;
	height: 2rem;
    width: 2rem;
    font-size: 1rem;
    border-radius: 1rem;
	color: #6400ff;
	background: rgb(247,253,233);
	background: -moz-linear-gradient(left, rgba(247,253,233,1) 0%, rgba(86,99,102,1) 85%);
	background: -webkit-linear-gradient(left, rgba(247,253,233,1) 0%,rgba(86,99,102,1) 85%);
	background: linear-gradient(to right, rgba(247,253,233,1) 0%,rgba(86,99,102,1) 85%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f7fde9', endColorstr='#566366',GradientType=1 );
}

.app-link {
  	color: #61dafb;
}


@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}
